const encoder = JSON.stringify;
const decoder = JSON.parse;

// Import BGG collection for a username, cache, and return games array
const importBGGCollection = (CACHE_KEY) => async (bggUsername) => {
  if (!bggUsername) throw new Error('No username provided');
  try {
    const response = await fetch(`https://boardgamegeek.com/xmlapi2/collection?username=${encodeURIComponent(bggUsername)}&own=1`);
    const xml = await response.text();
    if (xml.includes('<message>')) {
      throw new Error('Collection is being prepared by BoardGameGeek. Please wait and try again.');
    }
    const parser = new window.DOMParser();
    const doc = parser.parseFromString(xml, 'text/xml');
    const items = Array.from(doc.querySelectorAll('item'));
    const games = items.map((item) => {
      const id = item.getAttribute('objectid');
      const name = item.querySelector('name')?.textContent || '';
      const image = item.querySelector('image')?.textContent || '';
      // year published is not a direct child, so use getElementsByTagName
      const yearNode = item.getElementsByTagName('yearpublished')[0];
      const year = yearNode ? yearNode.textContent : '';
      const rank = item.querySelector('rank')?.textContent || '';
      const thumbnail = item.querySelector('thumbnail')?.textContent || '';
      return { id, name, image, year, rank, thumbnail };
    });
    localStorage.setItem(CACHE_KEY, encoder(games));
    return games;
  } catch (e) {
    throw e;
  }
};

const getCachedCollection = (CACHE_KEY) => () => {
  const cached = localStorage.getItem(CACHE_KEY);
  return cached ? decoder(cached) : null;
};

const getCachedItem = (CACHE_KEY) => (value) => {
  const cached = getCachedCollection(CACHE_KEY)()?.find((item) => item.name === value);
  return cached;
};

const gameCollectionUtil = (key) => {
  return {
    import: importBGGCollection(key),
    encoder: encoder,
    decoder: decoder,
    getAll: getCachedCollection(key),
    getOne: getCachedItem(key),
  };
};

export default gameCollectionUtil;
