import _ from 'lodash';

function random(seed) {
  var m = 2 ** 35 - 31;
  var a = 185852;
  var s = seed % m;
  return (s = (s * a) % m) / m;
}

export function stringToSeed(str) {
  let hash = 0;
  for (let i = 0; i < str.length; i++) {
    hash = str.charCodeAt(i) + ((hash << 5) - hash);
    hash = hash & hash; // Convert to 32bit integer
  }
  return Math.abs(hash);
}

const randomColor = (randomSeed) => {
  let numericSeed = _.isString(randomSeed) ? stringToSeed(randomSeed) : randomSeed;
  const randVal = !randomSeed ? Math.random() : random(numericSeed);
  return '#' + ('00000' + ((randVal * (1 << 24)) | 0).toString(16)).slice(-6);
};

export default randomColor;
