import React from 'react';
import styled from 'styled-components';

import storage from '../utils/storage';
import randomColor from '../utils/random-color';
import PieChart from './PieChart';

const Container = styled.div`
  display: flex;
  flex-flow: column;
  gap: 20px;
  flex-grow: 1;
  width: 100%;
`;

const WinnerTitle = styled.div`
  margin-bottom: 10px;
  letter-spacing: 1px;
  > h3 {
    width: 100%;
    display: inline-block;
    border-bottom: 1px solid var(--color-border);
  }
  > div {
    font-size: 20px;
    color: var(--color-success);
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    gap: 10px;
    align-items: center;
    > img {
      width: auto;
      height: 100%;
      max-width: 40%;
      max-height: 126px;
      object-fit: cover;
    }
  }
`;

const RoundBlock = styled.div`
  display: flex;
  flex-flow: column;
  .header-row {
    font-weight: 600;
    margin-bottom: 14px;
    font-size: 17px;
    color: var(--color-text);
    border-bottom: 1px solid var(--color-border);
    width: 100%;
    flex-grow: 1;
  }
  .pie-chart {
    flex-shrink: 0;
    flex-grow: 0;
    width: 125px;
    height: 125px;
    min-width: 125px;
    min-height: 125px;
  }
  .vote-results {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .vote-table {
    width: 100%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  .vote-row {
    font-size: 15px;
    display: grid;
    font-weight: 600;
    grid-template-columns: auto 32px;
    gap: 10px;
    width: 100%;
    flex-grow: 1;
  }
  .vote-none {
    display: flex;
    align-items: baseline;
    gap: 5px;
    flex-wrap: wrap;
    > span {
      font-size: 0.8em;
      color: var(--color-text-muted);
      display: inline-flex;
      align-items: center;
      font-style: italic;
    }
  }
  .vote-eliminated {
    margin-top: 10px;
    display: flex;
    align-items: baseline;
    gap: 5px;
    flex-wrap: wrap;
    > span {
      font-size: 0.8em;
      color: var(--color-text-muted);
      display: inline-flex;
      align-items: center;
      font-style: italic;
    }
  }
`;

export default function ResultsView({ results }) {
  // Store results in local history (top 50 by votes)
  React.useEffect(() => {
    if (!results) return;
    storage.results.set({
      winner: results.winner,
      votes: Object.keys(results.rounds?.[0]?.votes || {}),
      date: new Date().toISOString(),
    });
  }, [results]);

  const winnerGameDetails = React.useMemo(() => {
    return !results?.winner ? null : storage.games.getOne(results?.winner);
  }, [results?.winner]);

  if (!results) return null;
  const { winner, rounds } = results;

  return (
    <Container>
      <WinnerTitle>
        <h3>Winner</h3>
        <div>
          {winnerGameDetails?.thumbnail && <img src={winnerGameDetails?.thumbnail} alt={winnerGameDetails?.name} />}
          <span>{winner}</span>
        </div>
      </WinnerTitle>
      {rounds.map((round, i) => {
        const noVoteCandidate = round.candidates?.filter((c) => !(round.votes || {})[c]?.votes);
        return (
          <RoundBlock key={i} isLast={i === rounds.length - 1}>
            <strong className="header-row">
              Round {i + 1}
              {round.tieBreaker ? ' (Random Tie Breaker)' : ''}
            </strong>
            <div className="vote-results">
              <PieChart
                className={'pie-chart'}
                data={Object.values(round.votes || {}).map(({ candidate, votes }) => ({
                  key: candidate,
                  color: randomColor(candidate),
                  value: votes,
                }))}
                size={150}
              />
              <div className="vote-table">
                {Object.values(round.votes || {}).map(({ candidate, votes }) => {
                  return (
                    <div className="vote-row" key={candidate} style={{ color: randomColor(candidate) }}>
                      <div>{candidate}</div>
                      <div>{votes}</div>
                    </div>
                  );
                })}
                {!noVoteCandidate?.length ? (
                  <></>
                ) : (
                  <div className="vote-none">
                    <span>No Votes:</span>
                    {noVoteCandidate.map((name, ix) => (
                      <span key={name}>
                        {name}
                        {ix < noVoteCandidate.length - 1 ? ', ' : ''}
                      </span>
                    ))}
                  </div>
                )}

                {round.eliminated?.length && round.eliminated?.length === round.candidates?.length ? (
                  <div className="vote-eliminated test">
                    <strong>We have a tie!</strong>
                  </div>
                ) : (
                  round.eliminated?.length > 0 && (
                    <div className="vote-eliminated">
                      <strong>Eliminated:</strong>
                      {round.eliminated.map((name, ix) => (
                        <span key={name}>
                          {name}
                          {ix < round.eliminated.length - 1 ? ', ' : ''}
                        </span>
                      ))}
                    </div>
                  )
                )}
              </div>
            </div>
          </RoundBlock>
        );
      })}
    </Container>
  );
}
