/**
 * PieChart
 * Props:
 *   data: Array<{ key: string, color: string, value: number }>
 *   size?: number (default: 120)
 */
export default function PieChart({ className, data, size = 120 }) {
  // Calculate total value
  const total = data.reduce((sum, item) => sum + item.value, 0);
  let startAngle = 0;
  const center = size / 2;
  const radius = center - 2;

  // Helper to convert angle to SVG coordinates
  function polarToCartesian(cx, cy, r, angle) {
    const rad = ((angle - 90) * Math.PI) / 180;
    return {
      x: cx + r * Math.cos(rad),
      y: cy + r * Math.sin(rad),
    };
  }

  // Helper to describe SVG arc path
  function describeArc(cx, cy, r, startAngle, endAngle) {
    const start = polarToCartesian(cx, cy, r, endAngle);
    const end = polarToCartesian(cx, cy, r, startAngle);
    const largeArcFlag = endAngle - startAngle > 180 ? 1 : 0;
    return [`M ${cx} ${cy}`, `L ${start.x} ${start.y}`, `A ${r} ${r} 0 ${largeArcFlag} 0 ${end.x} ${end.y}`, 'Z'].join(' ');
  }

  // Build pie slices
  const slices = data
    .filter((z) => z.value > 0)
    .map((item, i) => {
      const angle = total ? (item.value / total) * 360 : 0;
      const slice = {
        ...item,
        startAngle,
        endAngle: startAngle + angle,
      };
      startAngle += angle;
      return slice;
    });

  // If only one slice, render a full circle
  if (slices.length === 1) {
    const slice = slices[0];
    return (
      <svg width={size} height={size} viewBox={`0 0 ${size} ${size}`} className={className}>
        <circle cx={center} cy={center} r={radius} fill={slice.color} stroke="#fff" strokeWidth="1" />
      </svg>
    );
  }

  return (
    <svg width={size} height={size} viewBox={`0 0 ${size} ${size}`} className={className}>
      {slices.map((slice, i) => (
        <path
          key={slice.key}
          d={describeArc(center, center, radius, slice.startAngle, slice.endAngle)}
          fill={slice.color}
          stroke="#fff"
          strokeWidth="1"
        />
      ))}
    </svg>
  );
}
