import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import storage from '../utils/storage';

const Container = styled.div`
  width: 100%;
  height: 100%;
  margin: 0 8px;
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  color: var(--color-text);
`;

const Label = styled.label`
  width: 100%;
  font-weight: bold;
  color: var(--color-accent-3);
`;

const FlexCol = styled.div`
  display: flex;
  flex-direction: row;
  gap: 8px;
  margin: 12px 0;
  > button {
    flex-shrink: 0;
    flex-grow: 0;
    width: 40px;
    height: 40px;
  }
`;

const FlexWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 16px;
  margin-top: 8px;
`;

const PastPlayersWrap = styled.div`
  margin-bottom: 18px;
`;

const PastPlayersFlex = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 8px;
`;

const BottomBar = styled.div`
  flex-grow: 1;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
`;

const MainButton = styled.button.attrs({ className: 'rcv-main-btn' })`
  width: 100%;
  padding: 12px 0;
  border-radius: 24px;
  font-weight: 700;
  font-size: 18px;
  border: none;
  box-shadow: var(--shadow-light);
  cursor: pointer;
  transition: background var(--transition-fast), box-shadow var(--transition-fast);
  margin-top: ${(props) => props.mt || 0}px;
  margin-bottom: ${(props) => props.mb || 0}px;
  background: var(--color-primary);
  color: var(--color-surface);
  font-size: ${(props) => props.fz || 18}px;
  box-shadow: ${(props) => props.bs || 'var(--shadow-light)'};
  border-radius: ${(props) => props.br || 24}px;
  max-width: ${(props) => props.maxWidth || 'none'};
  &:hover:not(:disabled) {
    background: var(--color-primary-dark);
    box-shadow: var(--shadow-medium);
  }
  &:disabled {
    background: #e0e0e0;
    color: #aaa;
    box-shadow: none;
    cursor: not-allowed;
  }
`;

const Pill = styled.span`
  background: ${(props) => (props.selected ? 'var(--color-primary)' : 'var(--color-surface)')};
  color: ${(props) => (props.selected ? 'var(--color-surface)' : 'var(--color-primary)')};
  border-radius: 999px;
  padding: 6px 5px 6px 15px;
  font-weight: 600;
  font-size: 16px;
  display: inline-flex;
  gap: 20px;
  align-items: center;
  justify-content: space-between;
  box-shadow: var(--shadow-light);
  position: relative;
  border: ${(props) => (props.selected ? 'none' : '1.5px solid var(--color-primary)')};
  margin-bottom: 4px;
  cursor: ${(props) => (props.selected ? 'default' : 'pointer')};
  transition: background var(--transition-fast), color var(--transition-fast), border var(--transition-fast);
`;

const RemoveBtn = styled.button`
  display: flex;
  right: 3px;
  background: var(--color-surface);
  color: var(--color-primary);
  border: 1.5px solid var(--color-primary-light);
  border-radius: 50%;
  font-size: 16px;
  cursor: pointer;
  min-height: 0;
  box-shadow: var(--shadow-light);
  transition: background var(--transition-fast), color var(--transition-fast);
  &:hover {
    background: var(--color-primary-light);
    color: var(--color-primary-dark);
  }
`;

const BGGBox = styled.div`
  margin: 12px 0 0 0;
  padding: 12px;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  background: var(--color-surface);
  width: 90%;
  max-width: 340px;
  box-shadow: var(--shadow-light);
`;

const BGGInput = styled.input`
  width: 100%;
  padding: 10px 14px;
  border-radius: 20px;
  border: 1.5px solid var(--color-primary);
  font-size: 15px;
  margin-bottom: 8px;
  outline: none;
  box-shadow: var(--shadow-light);
  background: var(--color-surface);
  color: var(--color-text);
  transition: border var(--transition-fast), box-shadow var(--transition-fast);
  &:focus {
    border: 1.5px solid var(--color-primary-dark);
    box-shadow: var(--shadow-medium);
  }
`;

export default function SetupView({ setStep, setNumVoters, setPlayerNames, players, setPlayers, pastPlayers, setPastPlayers }) {
  const [showBGG, setShowBGG] = useState(false);
  const [bggUsername, setBGGUsername] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [playerInput, setPlayerInput] = useState('');

  // Save current players to localHistory helper on change
  useEffect(() => {
    if (players.length === 0) return;
    setPlayerNames && setPlayerNames(players);
    setNumVoters && setNumVoters(players.length);
    const updatedAll = storage.members.set(players);
    setPastPlayers(updatedAll);
  }, [players]);

  // Add player
  const addPlayer = (name) => {
    const n = name.trim();
    if (n && !players.includes(n)) {
      setPlayers([...players, n]);
      setPlayerInput('');
    }
  };
  // Remove player
  const removePlayer = (name) => {
    setPlayers(players.filter((p) => p !== name));
  };
  // Re-add past player
  const readdPlayer = (name) => {
    if (!players.includes(name)) setPlayers([...players, name]);
  };

  async function importBGG() {
    setLoading(true);
    setError('');
    setSuccess('');
    try {
      const importedItems = await storage.games.import(bggUsername);
      setSuccess(`Imported ${importedItems?.length || 0} games!`);
    } catch (e) {
      setError(e?.message || 'Failed to fetch collection.');
    }
    setLoading(false);
  }

  return (
    <Container>
      {!players?.length ? (
        <></>
      ) : (
        <>
          <Label>Current Pod</Label>
          <FlexWrap>
            {players.map((name) => (
              <Pill key={name}>
                {name}
                <RemoveBtn type="button" aria-label={`Remove ${name}`} onClick={() => removePlayer(name)}>
                  x
                </RemoveBtn>
              </Pill>
            ))}
          </FlexWrap>
        </>
      )}
      {/* Past players not in current list as pill boxes for quick add */}
      {pastPlayers.filter((n) => !players.includes(n)).length > 0 && (
        <PastPlayersWrap>
          <Label>Past Pod</Label>
          <PastPlayersFlex>
            {pastPlayers
              .filter((n) => !players.includes(n))
              .map((n) => (
                <Pill key={n} onClick={() => readdPlayer(n)}>
                  {n}
                  <RemoveBtn type="button">+</RemoveBtn>
                </Pill>
              ))}
          </PastPlayersFlex>
        </PastPlayersWrap>
      )}

      <Label>Add To Pod</Label>
      <FlexCol>
        <BGGInput
          type="text"
          placeholder="Name"
          value={playerInput}
          onChange={(e) => setPlayerInput(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === 'Enter') addPlayer(playerInput);
          }}
        />
        <MainButton type="button" onClick={() => addPlayer(playerInput)} disabled={!playerInput.trim()}>
          +
        </MainButton>
      </FlexCol>
      <MainButton disabled={players.length < 1} onClick={() => setStep('candidate-mode')} fz={20} style={{ marginTop: 16 }}>
        Next
      </MainButton>

      <BottomBar>
        <MainButton onClick={() => setShowBGG((v) => !v)}>{showBGG ? 'Hide' : 'Import from BoardGameGeek'}</MainButton>
        {showBGG && (
          <BGGBox>
            <BGGInput placeholder="BoardGameGeek username" value={bggUsername} onChange={(e) => setBGGUsername(e.target.value)} />
            <MainButton disabled={!bggUsername || loading} onClick={importBGG} fz={16}>
              {loading ? 'Importing...' : 'Import'}
            </MainButton>
            {error && <div style={{ color: 'red' }}>{error}</div>}
            {success && <div style={{ color: 'green' }}>{success}</div>}
          </BGGBox>
        )}
      </BottomBar>
    </Container>
  );
}
