const encoder = JSON.stringify;
const decoder = JSON.parse;

const getPlayersCache = (PLAYERS_KEY) => () => {
  try {
    const raw = localStorage.getItem(PLAYERS_KEY);
    if (raw) return decoder(raw);
    return { last: [], all: [] };
  } catch {
    return { last: [], all: [] };
  }
};

// Set current and all players in localStorage
const setPlayersCache = (PLAYERS_KEY) => (players) => {
  try {
    const raw = localStorage.getItem(PLAYERS_KEY);
    let all = [];
    if (raw) {
      const parsed = decoder(raw);
      all = parsed.all || [];
    }
    const updatedAll = Array.from(new Set([...players, ...all]));
    localStorage.setItem(PLAYERS_KEY, encoder({ last: players, all: updatedAll }));
    return updatedAll;
  } catch {
    return [];
  }
};

const collectionUtil = (key) => {
  return {
    set: setPlayersCache(key),
    encoder: encoder,
    decoder: decoder,
    getAll: getPlayersCache(key),
  };
};

export default collectionUtil;
