const encoder = JSON.stringify;
const decoder = JSON.parse;

const MAX_HISTORY = 50;

const getCache = (CACHE_KEY) => () => {
  try {
    const raw = localStorage.getItem(CACHE_KEY);
    if (raw) return decoder(raw);
    return [];
  } catch {
    return [];
  }
};

// Set current and all items in localStorage
const setCache = (CACHE_KEY) => (items) => {
  if (!items) return;
  let results = [];
  try {
    const raw = localStorage.getItem(CACHE_KEY);
    if (raw) results = JSON.parse(raw);
  } catch {}
  // Add date if not present
  if (!items.date) items.date = new Date().toISOString();
  results.unshift(items);
  // Keep only last 50
  if (results.length > MAX_HISTORY) results = results.slice(0, MAX_HISTORY);
  localStorage.setItem(CACHE_KEY, JSON.stringify(results));
};

const getCacheItem = (CACHE_KEY) => (value) => {
  const cached = getCache(CACHE_KEY)()?.find((item) => item.name === value);
  return cached;
};

const collectionUtil = (key) => {
  return {
    set: setCache(key),
    encoder: encoder,
    decoder: decoder,
    getAll: getCache(key),
    getOne: getCacheItem(key),
  };
};

export default collectionUtil;
