import _ from 'lodash';

//Tally first choice votes for active candidates only
const tallyVote = (ballots, candidates) => {
  const length = ballots.length;
  return ballots
    .map((b) => b.filter((candidate) => candidates.includes(candidate))?.[0])
    .filter((candidate) => candidate)
    .reduce((acc, candidate) => {
      acc[candidate] = { candidate, votes: (acc[candidate]?.votes || 0) + 1 };
      acc[candidate].percent = acc[candidate].votes / length;
      return acc;
    }, {});
};
// Find candidate(s) with the lowest weighted votes
const findEliminated = (ballots, candidates) => {
  //go through  each ballot and sum weighted votes to find the lowest ones
  let votes = ballots.reduce((acc, ballot) => {
    //exclude candidates that are no longer active
    const activeBallot = ballot.filter((candidate) => candidates.includes(candidate));
    activeBallot.forEach((vote, ix) => (acc[vote] = (acc[vote] || 0) + (activeBallot.length - ix)));
    return acc;
  }, {});

  const lowestValue = Math.min(...Object.values(votes));
  return Object.keys(votes).reduce((acc, candidate) => {
    if (votes[candidate] <= lowestValue) acc.push(candidate);
    return acc;
  }, []);
};

export function calculateRCV(ballots, candidates) {
  const MAX_ROUNDS = 100; //safeguard against infinite loops
  let rounds = [];

  let winner = null;
  let activeCandidates = [...candidates];
  let activeBallots = [...ballots];

  // We are going to take the ballots and repeatedly tally them, removing the lowest candidate(s) each time
  while (!winner && activeCandidates.length > 0 && rounds.length < MAX_ROUNDS) {
    //step 1:tally the votes
    const votes = tallyVote(activeBallots, activeCandidates);
    winner = Object.values(votes).find((v) => v.percent > 0.5)?.candidate;
    const round = { candidates: [...activeCandidates], votes, winner, eliminated: [] };

    //step 2: find candidate(s) with fewest votes (Everyone tied is the fewest) and remove them from the ballots
    if (!winner) {
      round.eliminated = findEliminated(activeBallots, activeCandidates);
      activeCandidates = activeCandidates.filter((c) => !round.eliminated.includes(c));
    }

    // store the round results
    rounds.push(round);
  }

  // Make sure rounds are unique (can happen with ties)
  rounds = _.uniqBy(rounds, JSON.stringify);

  //everyone was eliminated in a tie - RANDOM winner
  if (!winner && activeCandidates.length === 0) {
    const lastRoundEliminated = rounds[rounds.length - 1]?.eliminated || [];
    winner = lastRoundEliminated[Math.floor(Math.random() * lastRoundEliminated.length)];
    rounds.push({
      winner,
      votes: lastRoundEliminated.map((candidate) => ({ candidate, ...(candidate === winner ? { votes: 1, percent: 1 } : { votes: 0, percent: 0 }) })),
      eliminated: lastRoundEliminated.filter((c) => c !== winner),
      tieBreaker: true,
    });
  }

  return { winner, rounds };
}
export function weightedVoting(ballots, candidates) {
  let active = [...candidates];
  let ballotCopies = ballots.map((b) => [...b]);

  let tally = {};
  active.forEach((c) => (tally[c] = 0));
  ballotCopies.forEach((b) => {
    const vLength = b.length;
    b.forEach((c, i) => {
      if (active.includes(c)) {
        tally[c] += vLength - i;
      }
    });
  });

  return {
    winner: Object.keys(tally).reduce((a, b) => (tally[a] > tally[b] ? a : b)),
    rounds: [tally],
  };
}

export const DEFAULT_ALGORITHM = calculateRCV;
