import gameCollection from './game-collection';
import members from './members';
import results from './results';

const COLLECTIONS = {
  games: gameCollection('bgg-candidates-cache-v1'),
  members: members('members-list-v1'),
  results: results('results-list-v1'),
  clearCache: () => {
    localStorage.removeItem('bgg-candidates-cache-v1');
    localStorage.removeItem('members-list-v1');
    localStorage.removeItem('results-list-v1');
  },
};

export default COLLECTIONS;
