import React, { useState, useRef } from 'react';
import SetupView from './components/SetupView';
import CandidateModeView from './components/CandidateModeView';
import PresetCandidatesView from './components/PresetCandidatesView';
import VoterNominationView from './components/VoterNominationView';
import VoterEntryView from './components/VoterEntryView';
import ResultsView from './components/ResultsView';
import storage from './utils/storage';
import { shuffleArray } from './utils/utils';

import styled from 'styled-components';

const HeaderRow = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  position: relative;
  width: 100%;
  > img {
    width: 50%;
    max-width: 200px;
    height: auto;
  }
  > div {
    color: var(--color-text);
    font-size: 42px;
    font-weight: bold;
    text-align: left;
  }
`;

const CogButton = styled.button`
  position: fixed;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
  padding: 0;
  width: 20px;
  cursor: pointer;
  z-index: 10;
  color: var(--color-primary-dark);
  font-size: 20px;
  display: flex;
  align-items: left;
  transition: color var(--transition-fast);
  &:hover {
    color: var(--color-primary);
  }
`;

const SettingsMenu = styled.div`
  position: fixed;
  top: 15px;
  right: 7px;
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: 10px;
  box-shadow: var(--shadow-light);
  min-width: 160px;
  padding: 8px 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: 10px;
`;

const MenuItem = styled.button`
  margin: 0;
  padding: 0 10px;
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  color: var(--color-text);
  font-size: 16px;
  cursor: pointer;
  transition: background var(--transition-fast);
  &:hover {
    background: var(--color-primary-light);
    color: var(--color-primary-dark);
  }
`;

function AppLayout({ children, resetApp }) {
  const [showMenu, setShowMenu] = useState(false);
  const menuRef = useRef();

  // Close menu on outside click
  React.useEffect(() => {
    function handleClick(e) {
      if (showMenu && menuRef.current && !menuRef.current.contains(e.target)) {
        setShowMenu(false);
      }
    }
    document.addEventListener('mousedown', handleClick);
    return () => document.removeEventListener('mousedown', handleClick);
  }, [showMenu]);

  const handleClearAllCache = () => {
    setShowMenu(false);
    if (!window.confirm('Are you sure you want to clear ALL local cache? This will remove all saved players, results, and imported games.')) {
      return;
    }
    storage.clearCache();
    alert('All local cache cleared.');
  };

  const wrapperStyle = {
    minHeight: '100vh',
    width: '100%',
    maxWidth: '100vw',
    height: '100%',
    background: 'var(--color-bg)',
    fontFamily: 'Inter, Segoe UI, Arial, sans-serif',
    padding: 0,
    margin: 0,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    transition: 'background var(--transition-medium)',
  };
  const cardStyle = {
    position: 'relative',
    background: 'var(--color-surface)',
    borderRadius: 20,
    boxShadow: '0 4px 24px 0 var(--shadow-medium)',
    border: '1px solid var(--color-border)',
    padding: '32px 28px',
    margin: '20px 10px',
    flexGrow: 1,
    maxWidth: '100%',
    height: '100%',
    zIndex: 2,
    transition: 'box-shadow var(--transition-medium)',
    display: 'flex',
    flexWrap: 'wrap',
    flexDirection: 'column',
    alignItems: 'center',
  };

  return (
    <div style={wrapperStyle}>
      <div style={cardStyle}>
        <HeaderRow>
          <img src="logo-trans.png" alt="Pod Sense Logo" />
          <div>
            Pod
            <br />
            Sense
          </div>
          <CogButton aria-label="Settings" onClick={() => setShowMenu((v) => !v)} title="Settings">
            ⚙️
          </CogButton>
          {showMenu && (
            <SettingsMenu ref={menuRef}>
              <MenuItem onClick={handleClearAllCache}>Clear Cache</MenuItem>
              <MenuItem
                onClick={() => {
                  setShowMenu(false);
                  resetApp?.();
                }}
              >
                Restart
              </MenuItem>
            </SettingsMenu>
          )}
        </HeaderRow>
        {children}
      </div>
    </div>
  );
}

export default function App() {
  const [step, setStep] = useState('setup');
  // Removed ballotName
  const [numVoters, setNumVoters] = useState(4);
  const [candidateMode, setCandidateMode] = useState(''); // "preset" or "voter"
  const [presetCandidates, setPresetCandidates] = useState('');
  const [bggUsername, setBGGUsername] = useState('');

  const [players, setPlayers] = useState([]);
  const [pastPlayers, setPastPlayers] = useState([]);
  const [selectedBGGGames, setSelectedBGGGames] = useState([]);
  const [numNoms, setNumNoms] = useState(1);
  const [voterNoms, setVoterNoms] = useState([]); // array of arrays
  const [voterBGGUsernames, setVoterBGGUsernames] = useState([]);
  const [voterBGGGames, setVoterBGGGames] = useState([]); // list of games per voter
  const [candidateList, setCandidateList] = useState([]);
  const [currentVoter, setCurrentVoter] = useState(0);
  const [rankings, setRankings] = useState([]); // array of arrays
  const [dragList, setDragList] = useState([]);
  const [results, setResults] = useState(null);
  // Per-voter nomination state for step 3B
  const [voterManual, setVoterManual] = useState([]); // array of strings, one per voter
  const [voterSelectedBGG, setVoterSelectedBGG] = useState([]); // array of arrays, one per voter

  const shuffleAndResetDragList = React.useCallback(() => {
    setDragList(shuffleArray(candidateList));
  }, [candidateList]);

  // Load last used players and all past players from localHistory helper
  React.useEffect(() => {
    const { last, all } = storage.members.getAll();
    setPlayers(last || []);
    setPastPlayers(all || []);
  }, []);
  // Ensure dragList is initialized with candidateList when entering voter-entry
  React.useEffect(() => {
    if (step === 'voter-entry' && (!dragList || dragList.length === 0) && candidateList.length > 0) {
      shuffleAndResetDragList();
    }
  }, [step, candidateList, dragList, shuffleAndResetDragList]);

  let body = null;
  if (step === 'setup') {
    body = (
      <SetupView
        numVoters={numVoters}
        setNumVoters={setNumVoters}
        setStep={setStep}
        players={players}
        setPlayers={setPlayers}
        pastPlayers={pastPlayers}
        setPastPlayers={setPastPlayers}
      />
    );
  } else if (step === 'candidate-mode') {
    body = <CandidateModeView players={players} setCandidateMode={setCandidateMode} setStep={setStep} />;
  } else if (step === 'preset-candidates') {
    body = (
      <PresetCandidatesView
        players={players}
        bggUsername={bggUsername}
        setBGGUsername={setBGGUsername}
        selectedBGGGames={selectedBGGGames}
        setSelectedBGGGames={setSelectedBGGGames}
        setCandidateList={setCandidateList}
        setStep={setStep}
        setCurrentVoter={setCurrentVoter}
        presetCandidates={presetCandidates}
        setPresetCandidates={setPresetCandidates}
        setDragList={setDragList}
      />
    );
  } else if (step === 'voter-nomination') {
    body = (
      <VoterNominationView
        players={players}
        numNoms={numNoms}
        setNumNoms={setNumNoms}
        numVoters={numVoters}
        voterNoms={voterNoms}
        setVoterNoms={setVoterNoms}
        voterBGGUsernames={voterBGGUsernames}
        setVoterBGGUsernames={setVoterBGGUsernames}
        voterBGGGames={voterBGGGames}
        setVoterBGGGames={setVoterBGGGames}
        candidateList={candidateList}
        setCandidateList={setCandidateList}
        setStep={setStep}
        setCurrentVoter={setCurrentVoter}
        bggUsername={bggUsername}
        voterManual={voterManual}
        setVoterManual={setVoterManual}
        voterSelectedBGG={voterSelectedBGG}
        setVoterSelectedBGG={setVoterSelectedBGG}
      />
    );
  } else if (step === 'voter-entry') {
    body = (
      <VoterEntryView
        players={players}
        currentVoter={currentVoter}
        numVoters={numVoters}
        candidateList={candidateList}
        dragList={dragList}
        setDragList={setDragList}
        shuffleAndResetDragList={shuffleAndResetDragList}
        rankings={rankings}
        setRankings={setRankings}
        setCurrentVoter={setCurrentVoter}
        setStep={setStep}
        setResults={setResults}
      />
    );
  } else if (step === 'results') {
    body = <ResultsView results={results} players={players} />;
  }
  return (
    <AppLayout
      resetApp={() => {
        setStep('setup');
        setCandidateList([]);
        setRankings([]);
        setResults(null);
      }}
    >
      {body}
    </AppLayout>
  );
}
